.App {
  font-family: serif;
  background-image: url("./components/pictures/chrisyang.jpg");
  background-color: black;
  text-align: center;
  max-width: 1200px;
}

h1 {
  background: orangered;
  font-family: cursive;
  border: 12px 12px;
  padding: 3px;
  letter-spacing: 4px;
  color: white;
  outline: rgb(245, 225, 225);
